<template>
  <div class="full">
    <div class="animation">
      <div style="position: relative; height: 100%; width: 100%; background-color: white;">
        <div :class="'round' + (item)" v-for="item in 1" :style="{backgroundColor: getRandomColor()}"></div>
      </div>
    </div>
    <div class="animation" style="height: 400px;">
      <div class="earth">
        <img src="../../assets/earth.png" alt="">
        <video id="myVideo" autoplay muted src='../../assets/ikun.mp4'></video>
      </div>
    </div>
    <div class="animation" style="height: 300px;">
      <!-- <div class="minute_wrap">
        <div class="minute_item" :style="{transform:'rotate('+30*(index +1)+'deg)'}" v-for="(item,index) in 12"
          :key="index">
          <div :class="[(isReverse % 2 == 0) ? 'startke' + (index + 1) : 'endke' + (index + 1)]"></div>
        </div>
        <div class="second_hand"></div>
      </div> -->
    </div>
    <div class="animation">
      <div class="g-container">
        <div class="g-group">
          <div class="item item-right"></div>
          <div class="item item-left"></div>
          <div class="item item-top"></div>
          <div class="item item-bottom"></div>
          <div class="item item-middle"></div>
        </div>
        <div class="g-group">
          <div class="item item-right"></div>
          <div class="item item-left"></div>
          <div class="item item-top"></div>
          <div class="item item-bottom"></div>
          <div class="item item-middle"></div>
        </div>
      </div>
    </div>
    <div class="animation">
      <div class="ball-box">
        <div class="first"></div>
        <div class="ball"></div>
        <div class="ball"></div>
        <div class="ball"></div>
        <div class="ball"></div>
        <div class="ball"></div>
        <div class="ball"></div>
        <div class="ball"></div>
      </div>
    </div>
    <div class="animation"></div>
    <div class="animation"></div>
    <div class="animation"></div>
    <div class="animation"></div>
    <div class="animation"></div>
    <div class="animation"></div>
    <div class="animation"></div>
    <div class="animation">
      <div class="pillarList">
        <p class="a">新年快乐</p>
        <p class="b">2024</p>
        <p class="a">HAPPY NEW YEAR</p>
      </div>
    </div>
    <div class="animation" style="height: 300px;">
      <div class="container">
        <div class="cloud"></div>
      </div>
    </div>
    <div class="animation1"
      style="background-color: black; width: calc((100% - 32px) / 3); height: 300px; overflow: hidden; display: flex; flex-wrap: wrap; transform-style: preserve-3d; perspective: 20px;">
      <div class="line" style="width: 2px; height: 50px; margin-right: 30px;"
        :style="{backgroundColor: getRandomColor()}" v-for="item in 100"></div>
    </div>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        isReverse: 0
      }
    },
    mounted() {
      
      var myVideo = document.getElementById('myVideo');
      myVideo.onended = function() {
        myVideo.play();
      };
      
      // 每隔100ms创建一个雨滴元素
      setInterval(() => this.rainEffect(), 100);
      const randomColorElement = document.querySelector('.line');
      const randomColor = this.getRandomColor();
      randomColorElement.style.setProperty('--random-color', randomColor);
      var seconds = 0;
      // setInterval(() => {
      //   seconds++;
      //   if (seconds % 12 == 0) {
      //     this.isReverse++;
      //   }
      // }, 1000)

    },
    methods: {
      getRandomColor() {
        const letters = '0123456789ABCDEF';
        let color = '#';
        for (let i = 0; i < 6; i++) {
          color += letters[Math.floor(Math.random() * 16)];
        }
        return color;
      },
      // 生成字母和数字数组
      generateText() {
        const letters = [];
        const numbers = [];
        const a = "a".charCodeAt(0);
        for (let i = 0; i < 26; i++) {
          letters.push(String.fromCharCode(a + i));

          if (i < 10000) {
            numbers.push(i + 1);
          }
        };
        return [...letters, ...numbers];
        // return ['¥', '$', '钱', '暴富'];
      },
      // 从生成的数组中随机取出一个字符
      randomText() {
        const texts = this.generateText();
        const text = texts[Math.floor(Math.random() * texts.length)];
        return text;
      },

      rainEffect() {
        const cloudEle = document.querySelector(".cloud");
        const textEle = document.createElement("div");
        textEle.innerText = this.randomText();
        // textEle.innerHTML = `<img style='width: 100%; height: 100%;' src='/img/100yuan.c974a66c.png' >`;
        textEle.classList.add("text");
        const left = Math.floor(Math.random() * 240);
        const size = Math.random() * 1.5;
        const duration = Math.random();
        const styleSheets = {
          left: `${left}px`,
          fontSize: `${0.5 + size}em`,
          animationDuration: `${1 + duration}s`,
          color:this.getRandomColor(),
        };
        Object.assign(textEle.style, styleSheets);
        cloudEle.appendChild(textEle);
        setTimeout(() => {
          cloudEle.removeChild(textEle);
        }, 2000);
      }
    }
  }
</script>

<style lang="scss">
  :root {
    // --primary-color: #fb9202;
    --primary-color: #fff;
  }


  .earth {
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;

    img {
      height: 100%;
    }

    video {
      position: absolute;
      left: 50%;
      transform: translateX(-50%);
      top: 60%;
      width: 100px;
      height: 150px;
      mix-blend-mode: multiply;
      filter: contrast(3);
    }
  }

  @for $i from 0 through 1 {
    .round#{$i} {
      position: absolute;
      left: 0;
      top: 0;
      width: 10px;
      height: 10px;
      background-color: red;
      border-radius: 50%;
      animation: horizontal 3s infinite alternate linear,
        vertical 1.9s infinite alternate linear,
        colorX 30s infinite steps(10),
        colorY 19s infinite steps(10);
      animation-delay: #{$i / 10}s;
    }
  }

  @keyframes horizontal {
    to {
      left: calc(100% - 10px);
    }
  }

  @keyframes vertical {
    to {
      top: calc(100% - 10px);
    }
  }

  @keyframes colorX {
    to {
      filter: hue-rotate(360deg);
    }
  }

  @keyframes colorY {
    to {
      filter: hue-rotate(360deg);
    }
  }

  .g-container {
    overflow: hidden;
    width: 100%;
    height: 100%;
    margin: auto;
    perspective: 4px;
    perspective-origin: 50% 50%;
    position: relative;
    animation: hueRotate 21s infinite linear;
  }

  .g-group {
    position: absolute;
    width: 1000px;
    height: 1000px;
    left: -500px;
    top: -500px;
    transform-style: preserve-3d;
    animation: move1 12s infinite linear;
  }

  .g-group:nth-child(2) {
    animation: move1 12s infinite linear;
    animation-delay: -6s;
  }

  .item {
    position: absolute;
    width: 100%;
    height: 100%;
    background: url(https://z3.ax1x.com/2021/08/20/fLwuMd.jpg);
    background-size: cover;
    opacity: 1;
    animation: fade 12s infinite linear;
    animation-delay: 0;
  }

  .g-group:nth-child(2) .item {
    animation-delay: -6s;
  }

  .item-right {
    transform: rotateY(90deg) translateZ(500px);
  }

  .item-left {
    transform: rotateY(-90deg) translateZ(500px);
  }

  .item-top {
    transform: rotateX(90deg) translateZ(500px);
  }

  .item-bottom {
    transform: rotateX(-90deg) translateZ(500px);
  }

  .item-middle {
    transform: rotateX(180deg) translateZ(1000px);
  }

  @keyframes move1 {
    0% {
      transform: translateZ(-500px) rotate(0deg);
    }

    100% {
      transform: translateZ(500px) rotate(0deg);
    }
  }

  @keyframes fade {
    0% {
      opacity: 0;
    }

    25%,
    60% {
      opacity: 1;
    }

    100% {
      opacity: 0;
    }
  }

  @keyframes hueRotate {
    0% {
      filter: hue-rotate(0);
    }

    100% {
      filter: hue-rotate(360deg);
    }
  }

  .ball-box {
    filter: blur(4px) contrast(8);
    position: relative;
    background-color: black;
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;

    .ball,
    .first {
      position: absolute;
      width: 40px;
      height: 40px;
      background: #fff;
      border-radius: 50%;
      transform: translate(-500%, 0);
      opacity: 0;
      // animation: move 3s infinite linear;
    }
  }

  @for $i from 0 through 7 {
    .ball:nth-child(#{$i}) {
      animation: move 3.5s infinite #{$i * 0.2 + 0.1}s linear;
    }
  }

  .first {
    animation: scaleMove 3.5s infinite linear;
  }

  @keyframes move {
    25% {
      opacity: 1;
      transform: translate(-0.5vw, 0);
    }

    50% {
      opacity: 1;
      transform: translate(1vw, 0);
    }

    75%,
    100% {
      opacity: 0;
      transform: translate(500%, 0);
    }
  }


  @keyframes scaleMove {
    25% {
      opacity: 1;
      transform: translate(-1vw, 0);
    }

    35% {
      opacity: 1;
      transform: scale(1);
    }

    70% {
      opacity: 1;
      transform: translate(1vw, 0) scale(2);
    }

    90%,
    100% {
      opacity: 0;
      transform: translate(1vw, 0) scale(1);
    }
  }

  .full {
    height: calc(100% - 16px);
    padding: 16px;
    background-color: #eee;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    overflow-y: scroll;
  }

  .animation {
    width: calc((100% - 32px) / 3);
    height: 300px;
    background-color: white;
    margin-bottom: 16px;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: black;
  }

  .animation .pillarList {
    transform: rotateX(25deg) rotateY(-25deg);
    margin-top: -30px;
  }

  .a {
    font-size: 45px;
    color: #88e;
    text-shadow: 0 0 0.3em rgba(200, 200, 255, 0.3), 0.04em 0.04em 0 #112,
      0.045em 0.045em 0 #88e, 0.09em 0.09em 0 #112, 0.095em 0.095em 0 #66c,
      0.14em 0.14em 0 #112, 0.145em 0.145em 0 #44a;
    animation: pulsea 800ms ease infinite alternate;
  }

  .b {
    font-size: 45px;
    color: #f99;
    text-shadow: 0 0 0.3em rgba(255, 100, 200, 0.3), 0.04em 0.04em 0 #112,
      0.045em 0.045em 0 #f99, 0.09em 0.09em 0 #112, 0.095em 0.095em 0 #b66,
      0.14em 0.14em 0 #112, 0.145em 0.145em 0 #a44;
    animation: pulseb 800ms ease infinite alternate;
  }

  @keyframes pulsea {
    from {
      text-shadow: 0 0 0.3em rgba(255, 100, 200, 0.3), 0.04em 0.04em 0 #112,
        0.045em 0.045em 0 #f99, 0.09em 0.09em 0 #112, 0.095em 0.095em 0 #b66,
        0.14em 0.14em 0 #112, 0.145em 0.145em 0 #a44;
    }

    to {
      text-shadow: 0 0 0.3em rgba(200, 200, 255, 0.3), 0.04em 0.04em 0 #112,
        0.045em 0.045em 0 #88e, 0.09em 0.09em 0 #112, 0.095em 0.095em 0 #66c,
        0.14em 0.14em 0 #112, 0.145em 0.145em 0 #44a;
    }
  }

  @keyframes pulseb {
    from {
      text-shadow: 0 0 0.3em rgba(200, 200, 255, 0.3), 0.04em 0.04em 0 #112,
        0.045em 0.045em 0 #88e, 0.09em 0.09em 0 #112, 0.095em 0.095em 0 #66c,
        0.14em 0.14em 0 #112, 0.145em 0.145em 0 #44a;
    }

    to {
      text-shadow: 0 0 0.3em rgba(255, 100, 200, 0.3), 0.04em 0.04em 0 #112,
        0.045em 0.045em 0 #f99, 0.09em 0.09em 0 #112, 0.095em 0.095em 0 #b66,
        0.14em 0.14em 0 #112, 0.145em 0.145em 0 #a44;
    }
  }

  .container {
    width: 100%;
    height: 200px;
    display: flex;
    justify-content: center;
    border-bottom: 1px solid rgba(255, 255, 255, .1);
    /* 添加一个从下往上线性渐变的镜像效果，增加视觉层次感 */
    -webkit-box-reflect: below 1px linear-gradient(transparent, transparent, transparent, transparent, #0005);
  }

  .cloud {
    position: relative;
    top: 0px;
    z-index: 100;
    width: 50%;
    height: 60px;
    background-color: var(--primary-color);
    border-radius: 30px;
    filter: drop-shadow(0 0 30px var(--primary-color));
  }

  .cloud::before {
    content: "";
    width: 80px;
    height: 70px;
    background-color: var(--primary-color);
    border-radius: 50%;
    position: absolute;
    top: -20px;
    left: 40px;
    box-shadow: 90px 0 0 10px var(--primary-color);
  }

  .cloud .text {
    position: absolute;
    top: 60px;
    height: 20px;
    width: 40px;
    line-height: 20px;
    text-transform: uppercase;
    color: var(--primary-color);
    /* 为文字添加阴影，看上去发光，增加视觉效果 */
    text-shadow: 0 0 5px var(--primary-color), 0 0 15px var(--primary-color), 0 0 30px var(--primary-color);
    transform-origin: bottom;
    animation: animate 2s linear forwards;
  }

  @keyframes animate {
    0% {
      transform: translateX(0);
    }

    70% {
      transform: translateY(120px);
    }

    100% {
      transform: translateY(120px);
    }
  }


  .line {
    transform: rotateX(45deg);
    animation: lineAni 4s infinite forwards;
  }

  @keyframes lineAni {
    from {
      transform: rotateX(45deg);
      height: 100px;
    }

    to {
      transform: rotateX(0deg);
      height: 0;
    }
  }

  .minute_wrap {
    width: 140px;
    height: 140px;
    background-color: white;
    position: relative;
    left: 0;
    top: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 70px;
  }

  .minute_item {
    top: 10px;
    position: absolute;
    width: 4px;
    height: calc(50%);
    text-align: center;
    transform-origin: center 60px;
    background-color: white;

  }

  $count: 12;

  @for $i from 1 through $count {
    .startke#{$i} {
      position: absolute;
      top: -8px;
      width: 4px;
      height: 8px;
      background-color: black;
      animation: startkeAnimate 0.6s forwards;
      animation-delay: #{$i - 0.2}s;
    }
  }

  $count: 12;

  @for $i from 1 through $count {
    .endke#{$i} {
      position: absolute;
      top: 80%;
      width: 4px;
      height: 0px;
      background-color: black;
      animation: endkeAnimate 0.6s forwards;
      animation-delay: #{$i - 0.1}s;
    }
  }

  @keyframes startkeAnimate {
    90% {
      top: 80%;
      opacity: 1;
      height: 0px;
    }

    100% {
      top: 80%;
      opacity: 0;
      height: 0px;
    }
  }

  @keyframes endkeAnimate {
    to {
      top: -8px;
      opacity: 1;
      height: 8px;
    }
  }

  .second_hand {
    width: 4px;
    height: 1px;
    background-color: #000;
    position: absolute;
    transform-origin: center bottom;
    transform: translateY(-50%);
    animation: second_hand 24s infinite linear;
  }

  @keyframes second_hand {
    0% {
      height: 1px;
      transform: translateY(-50%) rotate(0deg);
    }

    50% {
      height: calc(50% - 8px);
      transform: translateY(-50%) rotate(360deg);
    }

    100% {
      height: 1px;
      transform: translateY(-50%) rotate(720deg);
    }
  }
</style>
